  background-color: #f8f9fa;
}

.card-title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 16px;
}

.border-btn {
  border-top: 1px solid #ddd;
  margin-top: 8px;
  padding-top: 8px;
